
import axios from 'axios'

export const sberRegistration = (params, add_info) => {

  return axios.post(`${process.env.API_ROOT || window.API_ROOT || 'http://fapi.my'}/sber/registration`, params)
    .then(function (response) {
      // console.log(JSON.stringify(response));
      if (response.data.form_url) {
        new_order({
          order: {
            sber_id: response.data.order_id,
            url: response.data.form_url,
            govno_id: add_info.govno_id,
            amount: params.amount,
            name: add_info.name,
            email: add_info.email,
            phone: add_info.phone,
            datetime: add_info.datetime,
            tickets_count: add_info.tickets_count,
            park_name: add_info.park_name,
          },
        })
      } else {
        console.log(response.data.error)
      }
    })
    .catch(function (error) {
      console.log('error', error)
    })
}

// Сохранение пары номер заказа сбира и номер заказа говна на бэкэнде
export const new_order = (params) => {

  return axios.post(`${process.env.API_ROOT || window.API_ROOT || 'http://fapi.my'}/sber/orders`, params)
    .then(function (response) {
      if (response.data.state === 'ok') {
        window.location.replace(params.order.url)
      } else {
        console.log(JSON.stringify(response))
      }
    })
    .catch(function (error) {
      console.log(`не удалось сохранить заказ ${ error}`)
    })

}
